
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
nav {
  margin-bottom: 20px;
  position: fixed;
  width: 100%;
  background-color: #dcdcdc;
  Z-index: 5;
}
.logoutButton {
  border: none;
  background: none;
  margin-right: 10px;
}
.fa-power-off {
  font-size: 35px
}
h4 {
  font-size: 40px;
  margin: 10px 0px 10px 10px
}
h2 {
  font-size: 30px
}
label:hover {
  cursor: pointer
}



